/* ===== SIDEBAR CONTAINER ===== */
.sidebar-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 25;
    box-shadow: var(--shadow-lg);
    /* Remove transition property */
  }
  
  .sidebar-container.active {
    transform: translateX(0);
  }
  
  .sidebar {
    width: 100%;
    max-width: var(--sidebar-width);
    height: 100vh;
    background: linear-gradient(145deg, var(--dark-2) 0%, var(--dark-1) 100%);
    padding: clamp(1.5rem, 3vw, 2rem);
    position: fixed;
    left: 0;
    top: 0;
    z-index: 10;
    overflow-y: auto;
    border-right: 1px solid rgba(255, 255, 255, 0.08);
    transition: transform var(--transition-slow), box-shadow var(--transition-normal);
    transform: rotateY(-5deg) translateX(-10px) translateZ(var(--depth-1));
  background: linear-gradient(
    145deg, 
    rgba(30, 41, 59, 0.9) 0%, 
    rgba(15, 23, 42, 0.9) 100%
  );
  backdrop-filter: blur(10px);
  border-right: 2px solid rgba(59, 130, 246, 0.3);
  box-shadow: 
    30px 0 50px rgba(0, 0, 0, 0.3),
    inset 10px 0 20px rgba(59, 130, 246, 0.1);
  }
  
  .sidebar:hover {
    box-shadow: var(--shadow-lg);
  }
  
  .sidebar-inner {
    display: flex;
    flex-direction: column;
    height: 100%;
    gap: clamp(1.5rem, 3vw, 2rem);
  }
  
  /* ===== PROFILE CARD ===== */
  .profile-card {
    background: linear-gradient(145deg, var(--dark-2) 0%, var(--dark-3) 100%);
    border-radius: clamp(12px, 2vw, 16px);
    padding: clamp(1rem, 2vw, 1.5rem);
    border: 1px solid rgba(255, 255, 255, 0.1);
    box-shadow: var(--shadow-sm);
    transition: transform var(--transition-normal);
    transform: translateZ(var(--depth-2));
  background: linear-gradient(
    45deg,
    rgba(59, 130, 246, 0.1) 0%,
    rgba(30, 41, 59, 0.5) 100%
  );
  position: relative;
  overflow: hidden;
  }
  
  .profile-card:hover {
    transform: translateY(-3px);
  }
  
  .avatar-container {
    width: clamp(100px, 20vw, 120px);
    height: clamp(100px, 20vw, 120px);
    margin: 0 auto clamp(1rem, 2vw, 1.5rem);
    position: relative;
  }
  
  .avatar {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    border: 3px solid rgba(255, 255, 255, 0.1);
    transition: all var(--transition-normal);
  }
  
  .avatar:hover {
    border-color: rgba(59, 130, 246, 0.5);
    transform: scale(1.05);
  }
  
  .avatar-face {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform var(--transition-normal);
  }
  
  /* ===== PROFILE INFO ===== */
  .profile-info h1 {
    font-size: clamp(1.3rem, 3vw, 1.5rem);
    text-align: center;
    margin-bottom: clamp(0.2rem, 0.5vw, 0.25rem);
    color: var(--light-1);
  }
  
  .title {
    text-align: center;
    color: var(--light-3);
    margin-bottom: clamp(0.8rem, 2vw, 1rem);
    font-size: clamp(0.9rem, 2vw, 1rem);
    position: relative;
  }
  
  .title::after {
    content: '';
    display: block;
    width: 50px;
    height: 2px;
    background: linear-gradient(to right, var(--primary), transparent);
    margin: 0.5rem auto 0;
  }
  
  .location-chip {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    margin-top: 1rem;
    padding: 0.3rem 0.8rem;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 20px;
    font-size: 0.9rem;
  }
  
  /* ===== CONTACT INFO ===== */
  .contact-info {
    display: flex;
    flex-direction: column;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    margin: clamp(1rem, 2vw, 1.5rem) 0;
  }
  
  .contact-item {
    display: flex;
    align-items: center;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    font-size: clamp(0.8rem, 1.8vw, 0.9rem);
    transition: transform var(--transition-fast);
  }
  
  .contact-item:hover {
    transform: translateX(5px);
  }
  
  .contact-icon {
    width: clamp(28px, 5vw, 30px);
    height: clamp(28px, 5vw, 30px);
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 50%;
    transition: all var(--transition-normal);
  }
  
  .contact-item:hover .contact-icon {
    background: rgba(59, 130, 246, 0.2);
    transform: scale(1.1);
  }
  
  /* ===== QUICK STATS ===== */
  .quick-stats {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    margin-top: clamp(0.8rem, 2vw, 1rem);
  }
  
  .stat-item {
    text-align: center;
    padding: 0.5rem;
    border-radius: 8px;
    transition: all var(--transition-normal);
  }
  
  .stat-item:hover {
    background: rgba(255, 255, 255, 0.05);
  }
  
  .stat-value {
    font-size: clamp(1rem, 2.5vw, 1.1rem);
    font-weight: 700;
    color: var(--primary);
    transition: all var(--transition-normal);
  }
  
  .stat-label {
    font-size: clamp(0.65rem, 1.5vw, 0.7rem);
    opacity: 0.8;
  }
  
  /* ===== SIDEBAR NAV ===== */
  .sidebar-nav {
    display: flex;
    flex-direction: column;
    gap: clamp(0.4rem, 1vw, 0.5rem);
  }
  
  .nav-item {
    padding: clamp(0.6rem, 1.5vw, 0.75rem) clamp(0.8rem, 2vw, 1rem);
    border-radius: clamp(6px, 1.5vw, 8px);
    display: flex;
    align-items: center;
    gap: clamp(0.5rem, 1.5vw, 0.75rem);
    background: rgba(30, 41, 59, 0.6);
    border: 1px solid rgba(255, 255, 255, 0.15);
    transition: all var(--transition-normal);
    color: var(--light-1);
    font-size: clamp(0.85rem, 2vw, 1rem);
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  
  .nav-item:hover {
    background: rgba(30, 41, 59, 0.8);
    transform: translateX(5px);
  }
  
  .nav-item.active {
    background: linear-gradient(90deg, rgba(59, 130, 246, 0.8), rgba(99, 102, 241, 0.7));
    color: white;
    box-shadow: 0 5px 15px rgba(59, 130, 246, 0.3);
  }
  
  /* ===== TECH STACK ===== */
  .tech-stack {
    margin-top: clamp(0.8rem, 2vw, 1rem);
  }
  
  .tech-stack h4 {
    margin-bottom: clamp(0.5rem, 1.5vw, 0.75rem);
    font-size: clamp(0.9rem, 2vw, 1rem);
    display: flex;
    align-items: center;
    gap: 0.5rem;
  }
  
  .tech-tags {
    display: flex;
    flex-wrap: wrap;
    gap: clamp(0.3rem, 1vw, 0.5rem);
  }
  
  .tech-tag {
    padding: clamp(0.3rem, 1vw, 0.4rem) clamp(0.6rem, 1.5vw, 0.8rem);
    border-radius: 20px;
    font-size: clamp(0.75rem, 1.5vw, 0.8rem);
    background: rgba(255, 255, 255, 0.08);
    border: 1px solid rgba(255, 255, 255, 0.15);
    transition: all var(--transition-fast);
  }
  
  .tech-tag:hover {
    background: rgba(255, 255, 255, 0.15);
    transform: translateY(-2px);
  }
  
  .tech-tag.primary {
    background: rgba(59, 130, 246, 0.3);
  }
  
  .tech-tag.primary:hover {
    background: rgba(59, 130, 246, 0.4);
  }
  
  /* ===== RESPONSIVE ===== */
  @media (max-width: 768px) {
    .sidebar-inner {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
    
    .profile-card {
      width: 100%;
    }
    
    .sidebar-nav {
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
    }
    
    .nav-item {
      flex: 1 0 calc(50% - 1rem);
      justify-content: center;
    }
  }
  
  @media (max-width: 480px) {
    .sidebar {
      padding: 1.2rem;
    }
    
    .sidebar-inner {
      gap: 1.2rem;
    }
    
    .nav-item {
      flex: 1 0 100%;
    }
    
    .tech-tags {
      justify-content: center;
    }
  }

  .profile-card::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 200%;
    height: 200%;
    background: var(--glow);
    opacity: 0.1;
    animation: rotate 20s linear infinite;
  }
  
  @keyframes rotate {
    from { transform: rotate(0deg) translateZ(var(--depth-1)); }
    to { transform: rotate(360deg) translateZ(var(--depth-1)); }
  }
  
  .avatar {
    border: 2px solid rgba(59, 130, 246, 0.3);
    box-shadow: 
      0 0 30px rgba(59, 130, 246, 0.2),
      inset 0 0 20px rgba(59, 130, 246, 0.1);
  }
  
  .nav-item {
    transition: 
      transform var(--transition-normal),
      box-shadow var(--transition-normal),
      backdrop-filter var(--transition-normal);
    backdrop-filter: blur(5px);
  }
  
  .nav-item:hover {
    transform: translateX(10px) translateZ(var(--depth-1));
    box-shadow: 
      5px 5px 15px rgba(0, 0, 0, 0.3),
      inset 0 0 10px rgba(59, 130, 246, 0.2);
  }
  
  .tech-tag {
    transition: 
      transform var(--transition-fast),
      box-shadow var(--transition-fast);
    backdrop-filter: blur(5px);
  }
  
  .tech-tag:hover {
    transform: translateY(-3px) translateZ(10px);
    box-shadow: 0 5px 15px rgba(59, 130, 246, 0.3);
  }