/* ===== BASE STYLES & VARIABLES ===== */
:root {
  --dark-1: #0f172a;
  --dark-2: #1e293b;
  --dark-3: #334155;
  --light-1: #f8fafc;
  --light-2: #e2e8f0;
  --light-3: #94a3b8;
  --primary: #3b82f6;
  --secondary: #6366f1;
  --sidebar-width: clamp(280px, 25vw, 330px);
  --transition-fast: 0.2s ease;
  --transition-normal: 0.3s ease;
  --transition-slow: 0.4s cubic-bezier(0.22, 1, 0.36, 1);
  --shadow-sm: 0 5px 15px rgba(0, 0, 0, 0.2);
  --shadow-md: 0 10px 30px rgba(0, 0, 0, 0.3);
  --shadow-lg: 0 15px 35px rgba(0, 0, 0, 0.4);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: clamp(14px, 1.5vw, 16px);
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
  line-height: 1.6;
  color: var(--light-2);
  background-color: var(--dark-1);
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

/* ===== MAIN LAYOUT ===== */
.app-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
}

.layout-grid {
  display: flex;
  flex: 1;
  position: relative;
  transition: all var(--transition-slow);
}

.main-content {
  flex: 1;
  position: relative;
  z-index: 10;
  padding-left: var(--sidebar-width);
  transition: padding var(--transition-slow);
  will-change: padding;
}

/* ===== BACKGROUND ELEMENTS ===== */
.background-elements {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
  overflow: hidden;
}

.tech-circle {
  position: absolute;
  width: min(600px, 80vw);
  height: min(600px, 80vw);
  right: max(-200px, -20vw);
  bottom: max(-200px, -20vw);
  background: radial-gradient(circle, rgba(59, 130, 246, 0.1) 0%, transparent 70%);
  border-radius: 50%;
  filter: blur(clamp(20px, 5vw, 40px));
  opacity: 0.8;
  animation: pulse 15s ease-in-out infinite alternate;
}

.binary-grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(rgba(255, 255, 255, 0.03) 1px, transparent 1px),
    linear-gradient(90deg, rgba(255, 255, 255, 0.03) 1px, transparent 1px);
  background-size: clamp(30px, 5vw, 40px) clamp(30px, 5vw, 40px);
  opacity: 0.5;
}

.network-lines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(to right, 
      transparent calc(var(--sidebar-width) - 1px), 
      rgba(255, 255, 255, 0.05) var(--sidebar-width), 
      transparent calc(var(--sidebar-width) + 1px));
  transition: background-image var(--transition-normal);
}

/* ===== MOBILE HEADER ===== */
.mobile-header {
  display: none;
  align-items: center;
  justify-content: space-between;
  padding: clamp(0.8rem, 2vw, 1rem);
  background: var(--dark-2);
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  z-index: 30;
  position: sticky;
  top: 0;
  backdrop-filter: blur(8px);
}

.menu-toggle {
  background: none;
  border: none;
  color: var(--light-2);
  font-size: clamp(1.2rem, 3vw, 1.5rem);
  cursor: pointer;
  transition: transform var(--transition-normal);
  padding: 0.5rem;
  border-radius: 50%;
}

.menu-toggle:hover {
  transform: scale(1.1);
  background: rgba(255, 255, 255, 0.1);
}

.mobile-title {
  font-size: clamp(1rem, 3vw, 1.2rem);
  font-weight: 600;
  margin-left: clamp(0.5rem, 2vw, 1rem);
  background: linear-gradient(90deg, var(--light-1), var(--light-3));
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

/* ===== ANIMATIONS ===== */
@keyframes pulse {
  0% { opacity: 0.5; transform: scale(0.95); }
  100% { opacity: 0.8; transform: scale(1.05); }
}

@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 1024px) {
  .main-content {
    padding-left: 0;
  }

  .network-lines {
    background-image: none;
  }

  .mobile-header {
    display: flex;
  }

  .main-content.active {
    transform: translateX(var(--sidebar-width));
  }
}

@media (max-width: 768px) {
  .tech-circle {
    right: -30vw;
    bottom: -30vw;
  }
}

@media (max-width: 480px) {
  .main-content {
    padding: 1rem;
  }
}

:root {
  /* Add 3D effect variables */
  --perspective: 2000px;
  --depth-1: 20px;
  --depth-2: 40px;
  --depth-3: 60px;
  --glow: radial-gradient(circle at 50% 50%, 
    rgba(59, 130, 246, 0.4), 
    rgba(99, 102, 241, 0.2), 
    transparent 70%
  );
}

body {
  perspective: var(--perspective);
  transform-style: preserve-3d;
}

/* Neon text effect */
.section-title {
  text-shadow: 
    0 0 10px rgba(59, 130, 246, 0.4),
    0 0 20px rgba(99, 102, 241, 0.3),
    0 0 30px rgba(255, 255, 255, 0.2);
  position: relative;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 100%;
  height: 2px;
  background: var(--glow);
  filter: blur(5px);
}

@keyframes hologram-pulse {
  0% { opacity: 0.3; transform: translateZ(var(--depth-1)); }
  50% { opacity: 0.6; transform: translateZ(var(--depth-2)); }
  100% { opacity: 0.3; transform: translateZ(var(--depth-1)); }
}

.glowing-element {
  animation: hologram-pulse 3s ease-in-out infinite;
}

@keyframes scanline {
  from { background-position: 0 0; }
  to { background-position: 0 100%; }
}

.hologram-effect::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    to bottom,
    transparent 50%,
    rgba(59, 130, 246, 0.1) 51%,
    transparent 52%
  );
  opacity: 0.3;
  animation: scanline 5s linear infinite;
}